import React, { Component } from 'react';
import $ from "jquery";
class Accordion extends Component {
    onShowHide = e => {
        let tar = e.target.nextSibling.nextSibling;
        let checks = document.querySelectorAll(".AccordianCheck");
        if (e.target.checked) {
            const dropDown = document.querySelectorAll(".dropDownAccord");
            const icons = document.querySelectorAll(".AccordionLabel");
            for (let i = 0; i < icons.length; i++) {
                icons[i].childNodes[1].classList.remove("fa-chevron-up");
                icons[i].childNodes[1].classList.add("fa-chevron-down");
            }
            for (let i = 0; i < dropDown.length; i++) {
                if (checks[i].id !== e.target.id) {
                    checks[i].checked = false;
                }
                $(dropDown[i]).slideUp();
            }
            $(tar).slideDown();
            tar.style.display = this.props.display;
            e.target.nextSibling.childNodes[1].classList.remove("fa-chevron-down");
            e.target.nextSibling.childNodes[1].classList.add("fa-chevron-up");

        }
        else {
            $(tar).slideUp();
            e.target.nextSibling.childNodes[1].classList.remove("fa-chevron-up");
            e.target.nextSibling.childNodes[1].classList.add("fa-chevron-down");
        }
    }
    render() {
        return (
            <React.Fragment>
                {this.props.name === "First" ? (<input type="checkbox" onClick={this.onShowHide} defaultChecked={true} id={this.props.name} className="d-none AccordianCheck" />) : (<input type="checkbox" onClick={this.onShowHide} id={this.props.name} className="d-none AccordianCheck" />)}
                <label className="AccordionLabel" htmlFor={this.props.name}><span>{this.props.label}</span>
                    <i className="fas fa-chevron-down"></i>
                </label>
                <div className={`Content dropDownAccord ${this.props.name === "First" ? "flex-md-row flex-column align-items-md-center align-items-start" : ""}`} style={{ display: this.props.name === "First" ? "flex" : "none" }}>
                    {this.props.children}
                </div>
            </React.Fragment>
        )
    }
}
export default Accordion;