import React, { Component } from 'react';
class Popup extends Component {
    render() {
        return (
            <div className={`PopUp ${this.props.classes}`} style={{ display: "none" }}>
                <i className="fas fa-caret-up"></i>
                <div className="Content">
                    {this.props.children}
                </div>
            </div>
        )
    }
}
export default Popup;