import React, { Component } from "react";
import $ from "jquery";
import POPUP from "../common/Popup";
import { Menubar } from "../../redux/actions/index";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
class MainNav extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Msg: [
        {
          img: "./img/msg.png",
          name: "Taylor Devis",
          time: "1:45 PM",
          des: "Added You As Friend",
          type: "Notification"
        },
        {
          img: "./img/msg.png",
          name: "Taylor Devis",
          time: "1:45 PM",
          des: "Added You As Friend",
          type: "Notification"
        },
        {
          img: "./img/msg.png",
          name: "Taylor Devis",
          time: "1:45 PM",
          des: "Added You As Friend",
          type: "Notification"
        },
        {
          img: "./img/msg.png",
          name: "Taylor Devis",
          time: "1:45 PM",
          des: "Untitled Message....",
          type: "Message"
        },
        {
          img: "./img/msg.png",
          name: "Taylor Devis",
          time: "1:45 PM",
          des: "Untitled Message....",
          type: "Message"
        }
      ],

      dropMenu: ["Comedies", "Sci Fi", "Action"],
      UserDrop: [
        { img: "settings", name: "Setting", link: "/profile-setting" },
        { img: "help", name: "Help", link: "" },
        { img: "exit", name: "Sign Out", link: "" }
      ],
      name: "Alexander "
    };
  }
  showDropDown = e => {
    if (
      e.target.id !== undefined &&
      e.target.id !== null &&
      e.target.id !== ""
    ) {
      const Pop = document.querySelectorAll(".PopUp");
      for (let i = 0; i < Pop.length; i++) {
        if (Pop[i].className.split(" ")[1] !== e.target.id) {
          $(Pop[i]).slideUp();
        }
      }
      $(`.${e.target.id}`).slideToggle();
    }
  };
  menuHandler = e => {
    this.props.openMenu();
  };
  componentWillReceiveProps = props => {
    const { name } = props.Login;
    this.setState({
      name
    });
  };
  render() {
    const UserDropDown = this.state.UserDrop.map((v, k) => {
      return (
        <div className="msgContainer" key={k}>
          <img src={`./img/icon/${v.img}.png`} className="img-fluid" alt="" />
          <Link to={v.link}>{v.name}</Link>
        </div>
      );
    });
    const Msg = this.state.Msg.map((v, k) => {
      if (v.type === "Message") {
        return (
          <div className="msgContainer" key={k}>
            <div className="Dp">
              <img src={v.img} className="img-fluid" alt="" />
            </div>
            <div className="content">
              <p>{v.name}</p>
              <span>{v.des}</span>
            </div>
            <p className="time">{v.time}</p>
          </div>
        );
      }
    });
    const Noti = this.state.Msg.map((v, k) => {
      if (v.type === "Notification") {
        return (
          <div className="msgContainer" key={k}>
            <div className="Dp">
              <img src={v.img} className="img-fluid" alt="" />
            </div>
            <div className="content">
              <p>{v.name}</p>
              <span>{v.des}</span>
            </div>
            <p className="time">{v.time}</p>
          </div>
        );
      }
    });
    const dropDown = this.state.dropMenu.map((v, k) => {
      return (
        <div className="MenuContainer" key={k}>
          <p>{v}</p>
        </div>
      );
    });
    return (
      <React.Fragment>
        <nav
          className="MainNav py-md-5  p-0 col-12"
          style={{
            backgroundColor:
              this.props.color === undefined ? undefined : this.props.color
          }}
        >
          <div className="container-fluid">
            <div
              className={`row ${
                this.props.display !== true
                  ? "justify-content-between"
                  : "justify-content-center"
              }`}
            >
              <div className="Logo col-md-2 col-sm-6 col-6 order-md-0 order-1">
                <Link to="/">
                  {" "}
                  <img
                    src="./img/logo.png"
                    className="img-fluid ml-md-2 m-0 p-5 p-md-3"
                    alt=""
                  />
                </Link>
              </div>
              <div
                className={`NavItem col-md-6 col-sm-3 col-3 order-md-1 order-0 justify-content-md-end ${
                  this.props.display !== true ? "d-none" : "d-flex"
                }`}
              >
                <img
                  src="./img/bars.png"
                  className="d-md-none d-block img-fluid ml-3"
                  onClick={this.menuHandler}
                  alt=""
                />
                <ul className="d-md-flex d-none">
                  {/* <li id="drop" onClick={this.showDropDown}>
                    <span id="drop">
                      Movies
                    </span>
                  </li> */}
                  <li
                    id="drop"
                    onClick={this.showDropDown}
                    style={{ position: "relative" }}
                  >
                    Home<i id="drop" className="fas fa-chevron-down"></i>
                    <POPUP classes="drop">{dropDown}</POPUP>
                  </li>
                  <li>
                    tv shows<i id="drop" className="fas fa-chevron-down"></i>
                  </li>
                  <li>
                    web series<i id="drop" className="fas fa-chevron-down"></i>
                  </li>
                  <li>
                    trailers<i id="drop" className="fas fa-chevron-down"></i>
                  </li>
                  <li>
                    channels<i id="drop" className="fas fa-chevron-down"></i>
                  </li>
                </ul>
              </div>
              <div className="col-md-4 col-sm-3 col-3 order-2 icons">
                <div className="MainIcon  order-md-0 order-1">
                  <Link to="/search">
                    <div className="Icons">
                      <div className="Inside">
                        <img src="./img/icon/search.png" alt="" />
                      </div>
                    </div>
                  </Link>
                </div>
                <div
                  className="MainIcon d-md-flex d-none"
                  id="Msg1"
                  onClick={this.showDropDown}
                >
                  <div className="Icons" id="Msg1">
                    <div className="Inside" id="Msg1">
                      <img src="./img/icon/msg.png" id="Msg1" alt="" />
                    </div>
                  </div>
                  <POPUP classes="Msg1 mt-5">{Msg}</POPUP>
                </div>
                <div
                  className="MainIcon order-md-1 order-0"
                  id="Bell1"
                  onClick={this.showDropDown}
                >
                  <div className="Icons" id="Bell1">
                    <div className="Inside" id="Bell1">
                      <img src="./img/icon/bell.png" id="Bell1" alt="" />
                    </div>
                  </div>
                  <POPUP classes="Bell1">{Noti}</POPUP>
                </div>
                <div
                  className="MainIcon flex-row  order-md-2 order-2"
                  id="userProfile"
                  onClick={this.showDropDown}
                >
                  <div className="Icons" id="userProfile">
                    <img src="./img/dp.png" alt="" id="userProfile" />
                  </div>
                  <p className="d-md-inline d-none" id="userProfile">
                    {this.state.name}
                  </p>
                  <i className="fas fa-caret-down" id="userProfile"></i>
                  <POPUP classes="userProfile">{UserDropDown}</POPUP>
                </div>
              </div>
            </div>
          </div>
        </nav>
      </React.Fragment>
    );
  }
}
const mapStateToProps = state => {
  return {
    ...state
  };
};
const mapDispatchToProps = dispatch => {
  return {
    openMenu: () => {
      dispatch(Menubar.openMenu());
    }
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(MainNav);
